.overflow_hidden {
	overflow: hidden;
}

.btn_w200 {
	min-width: 200px;
}
.bold {
	font-weight: 600 !important;
}

.page-title {
	margin-bottom: $font-size-base;
	&.with-margin {
		margin-bottom: $font-size-base * 3;
	}
	.title {
		font-size: 125%;
		margin-bottom: $font-size-base;
	}

	.sub-title {
		font-size: 100%;
		font-family: $font-family-poppins;
		font-weight: 500 !important;
		margin-bottom: $font-size-base;
		margin-top: $font-size-base * 1.5;
	}
	.thankyou-file {
		margin-top: $font-size-base * 3;
		.text {
			font-size: 80%;
			margin-left: $font-size-base * 0.5;
		}
	}
	@include media-breakpoint-up(sm) {
		.title {
			font-size: 200%;
		}
		.sub-title {
			font-size: 150%;
			margin-top: $font-size-base * 2.2;
		}
	}
}
.sec-header {
	font-size: 100%;
	font-weight: 600;
	margin-bottom: $font-size-base * 2;
	// color: darken($primary, 30%);
	padding: 0 $font-size-base * 2;
}

.modal-page-list {
	padding: 0 $font-size-base * 2;
	.table {
		margin: $font-size-base auto;
		border: 1px solid $table-border-color;
	}
	.header {
		font-size: 90%;
		font-weight: 600;
		margin-bottom: $font-size-base * 0.5;
		color: darken($primary, 20%);
	}

	.page-list {
		margin: 0;
		padding: 0;
		margin-bottom: $font-size-base * 2;
		li {
			list-style: none;
			padding-left: $font-size-base;
			position: relative;
			margin-bottom: $font-size-base * 0.3;
			font-size: 90%;
			&:after {
				content: '';
				width: $font-size-base * 0.3;
				height: $font-size-base * 0.3;
				background-color: $gray-900;
				position: absolute;
				top: $font-size-base * 0.5;
				left: 0;
				border-radius: $border-radius-lg;
			}
		}
		ul {
			margin: $font-size-base auto;
		}
	}
}
.btn-radio-flex,
.btn-check-flex {
	display: flex;
}

.form-group-box {
	label {
		font-weight: 600;
		font-family: $font-family-poppins;
		font-size: 85%;
		display: block;
		margin-bottom: $font-size-base * 0.4;
	}
	position: relative;
	margin-bottom: $form-group-margin;
}

.btn-radio-box,
.btn-check-box {
	display: block;
	padding: $font-size-base * 0.4 $font-size-base * 1.5;
	position: relative;
	border: 1px solid lighten($primary, 35%);
	margin-right: $font-size-base * 0.4;
	border-radius: $border-radius;

	input {
		cursor: pointer;
		position: absolute;
		height: 100%;
		width: 100%;
		top: 0;
		left: 0;
		@include opacity(0);
		z-index: 10;
	}
	&.active {
		border-color: lighten($primary, 20%);
		background-color: lighten($primary, 45%);
	}

	&.disabled {
		cursor: not-allowed;
		pointer-events: none;
		filter: none;
		border-color: $gray-500;
		@include opacity(0.5);
		input {
			pointer-events: none;
			filter: none;
			cursor: not-allowed;
		}
	}
}
.no-display {
	display: none;
}

.button-margin {
	margin-top: $font-size-base * 5 !important;
	&-sm {
		margin-top: $font-size-base * 3 !important;
	}
	&-xs {
		margin-top: $font-size-base * 1.5 !important;
	}
	&-lg {
		margin-top: $font-size-base * 5 !important;
	}
}
